import {check} from './commonApi.js';
import {pattern} from './validator.js';
module.exports = {
	/**
	 * 
	 * @Author   szh
	 * @DateTime 2018-03-02
	 * cfield(String) 
	 */
	checkValid: (rule, value, callback) => {
		let params = {
			id: rule['id'],
			field: rule['cfield'],
			value: value,
		};
		check(rule['route'], params)
			.then(data => {
				if (data.res === true) {
					callback();
				} else {
					callback(new Error(`${rule['label']}`));
				}
			});
	},
	/**
	 * 
	 * @Author   szh
	 * @DateTime 2018-06-11
	 * length(Array) length: [2, 10]
	 */
	lengthValid: (rule, value, callback) => {
		if (value.length < rule['length'][0] || value.length > rule['length'][1]) {
			callback(new Error(`${rule['label']}${rule['length'][0]}${rule['length'][1]}`));
		} else {
			callback();
		}
	},
	/**
	 * 
	 * @Author   szh
	 * @DateTime 2018-06-11
	 * pattern(String) emailphone
	 */
	patternValid: (rule, value, callback) => {
		if (!rule['required'] && !value) { //  
			callback();
		}
		if (value && pattern[rule['pattern']](value)) {
			callback();
		} else {
			callback(new Error(`${rule['label']}`));
		}
	},
	// /**
	//  * 
	//  * @Author   szh
	//  * @DateTime 2018-06-11
	//  */
	// accountValid: function(rule, value, callback) {
	// 	console.log(pattern['email'](value));
	// 	console.log(pattern['phone'](value));
	// 	console.log(value.length >= 2 && value.length <= 10);
	// 	if (
	// 		pattern['email'](value) ||
	// 		pattern['phone'](value) ||
	// 		(value.length >= 2 && value.length <= 10)
	// 	) {
	// 		callback();
	// 	} else {
	// 		callback(new Error(``));
	// 	}
	// }
};



